@charset "utf-8";
@import "_mixin.scss";

$base_font__color: #444;
$link-color: #444;

body {
	line-height: 1.75;
	@include font-size(16);
	word-wrap : break-word;
	overflow-wrap : break-word;
	font-weight: normal;
	font-family:Avenir, Verdana, Arial, "ヒラギノ角ゴ ProN W3", "Hiragino Kaku Gothic ProN", "Hiragino Kaku Gothic Pro", "メイリオ", Meiryo, sans-serif;
	text-rendering: optimizeLegibility;
	color: $base_font__color;
	text-align: justify;
}

a, a:link, a:active, a:visited {text-decoration: none;outline: none;}
a:hover, a:active {outline: 0;}
a, a:visited, a:focus, a:active {color: $link-color;}
a:hover {color: $link-color;}

/*-----------------------------------------------
 __fontfamily
-----------------------------------------------*/
.serif {
	font-family: 'Times New Roman', '游明朝', YuMincho, 'ヒラギノ明朝 ProN W3', 'Hiragino Mincho ProN', 'HG明朝E', '小塚明朝 ProN', 'メイリオ', Meiryo, serif;
}

